.user{

    &__wrapper{
        border: 3px solid rgb(156, 60, 188);
        border-radius: 20px;
        box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
        text-align: center;
        position: relative;
        padding: 1vw;
        background-color: white;
    }
    &__name{
        margin: 0.5vw;
        font-size: 2em;
    }
    &__username{
        font-size: 1.7em;
        margin: 0;
    }
    &__id{
        position: absolute;
        font-size: 70px;
        z-index: -1;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        margin: 0;
        color: #e0cedd
    }
    &__company{
        font-size: 1.5em;
        margin: 5px 0;
    }

    &__email, &__phone{
        color: rgb(128, 9, 179);
        font-size: 1.4em;
        font-style: italic;
        text-decoration: none;
        position: relative;
        &:after {
            display: block;
            position: absolute;
            left: 0;
            width: 0;
            height: 3px;
            background-color: rgb(102, 21, 102); 
            content: "";
            transition: width 0.3s ease-out; 
        }
        &:hover:after,
        &:focus:after {
	        width: 100%; 
        }
    }
    &__website{
        padding: 0.4vw 0.6vw;
        background-color: rgb(156, 60, 188);
        color: white;
        border-radius: 20px;
        text-decoration: none;
        position: relative;
        &:after {
            display: block;
            position: absolute;
            left: 0;
            width: 0;
            height: 3px;
            background-color: white; 
            content: "";
            transition: width 0.3s ease-out; 
        }
        &:hover:after,
        &:focus:after {
	        width: 100%; 
        }
    }
    &__address{
        font-size: 1.3em;
        margin: 0.5vw;
    }

    &__posts{
        position: relative;
        text-decoration: none;
        font-size: 1.6em;
        &:after {
            display: block;
            position: absolute;
            left: 0;
            width: 0;
            height: 3px;
            background-color: rgb(102, 21, 102); 
            content: "";
            transition: width 0.3s ease-out; 
        }
        &:hover:after,
        &:focus:after {
	        width: 100%; 
        }
    }
    &__open-albums{
        border: none;
        margin: 0.4vw;
        font-size: 1.6em;
        transition: all 300ms ease; 
        border-radius: 5px;
        padding: 0.4vw;
        cursor: pointer;
        &:hover {
            background: rgba(0,0,0,0);
            color: rgb(102, 21, 102);
            box-shadow: inset 0 0 0 3px rgb(102, 21, 102);
        }
    }
}
.album{
    margin: 1vw 0;
    font-weight: 500;
}